    position: relative;
}
.u-content h1:after,
.h1:after {
    content: '';
    display: block;
    border-bottom: solid 2px var(--base-darked2);
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 200px;
}
.u-content h1:before,
.h1:before {
    content: '';
    display: block;
    border-bottom: solid 2px var(--base-darked2);
    position: absolute;
    bottom: -7px;
    left: 0;
    width: 100px;
}

.u-content h2,
.h2 {
